.forms-elements {
  --column-template: 320px 126px 320px 72px 320px;

  padding: 143px 140px;

  &__container {
    display: grid;
    grid-template-columns: var(--column-template);
  }

  &__row {
    display: grid;
    grid-column: 1 / -1;
    grid-template-columns: var(--column-template);

    &--1 {
      margin-bottom: 100px;
    }

    &--2 {
      margin-bottom: 15px;
    }

    &--3 {
      margin-bottom: 211px;
    }
  }

  &__column {
    &--left {
      grid-column: 1 / 2;
    }

    &--center {
      grid-column: 3 / 4;
    }

    &--right {
      grid-column: 5 / 6;
    }

  }

  // ----- row--1 ----- //
  // ----- column left ----- //
  &__text-field {
    margin-bottom: 20px;
  }

  &__text-field-focus {
    margin-bottom: 20px;
  }

  &__dropdown-people {
    margin-bottom: 24px;
  }

  &__masked-text-field {
    margin-bottom: 40px;
  }

  &__date-dropdown {
    margin-bottom: 43px;
  }

  &__filter-date-dropdown {
    margin-bottom: 40px;
    max-width: 266px;
  }

  &__subscription-text-field {
    max-width: 266px;
  }

 // ----- row--1 ----- //
  // ----- column center ----- //
  &__checkbox-buttons {
    margin-bottom: 39px;
    max-width: 222px;
  }

  &__radio-buttons {
    margin-bottom: 39px;
  }

  &__toggle {
    margin-bottom: 43px;
  }

  &__like-button {
    margin-bottom: 38px;
  }

  &__like-button-title {
    margin-bottom: 12px;
  }

  &__like-button-line {
    display: flex;
    gap: 10px;
  }

  &__rate-buttons-title {
    margin-bottom: 12px;
  }

  &__rate-buttons-container {
    display: flex;
    justify-content: space-between;
    max-width: 260px;
  }


   // ----- row--1 ----- //
  // ----- column right ----- //
  &__range-slider{
    margin-bottom: 38px;
    max-width: 266px;
  }

  &__buttons-title {
    margin-bottom: 12px;
  }

  &__buttons-container {
    display: flex;
    flex-direction: column;
    margin-bottom: 37px;
    gap: 30px;
  }

  &__button-mini {
    display: flex;
    height: 44px;
    max-width: 218px;
    gap: 20px;
  }

  &__button-text {
    display: flex;
    max-width: 218px;
    gap: 20px;
  }

  &__button--arrow {
    height: 44px;
  }

  &__pagination {
    display: flex;
    flex-direction: column;
    gap: 30px;
  }


  // ----- row--2----- //
  // ----- column left ----- //

   &__dropdown {
    position: relative;
    z-index: 1;
    grid-column: 1 / 2;
    margin-bottom: 22px;
    max-width: 266px;
  }

  &__dropdown-open {
    grid-column: 1 / 2;
    margin-bottom: 158px;
    max-width: 266px;
  }

  // ----- row--2----- //
  // ----- column center ----- //

  &__dropdown-guest-zero {
    grid-column: 3 / 4;
  }

  // ----- row--2----- //
  // ----- column right ----- //

  &__dropdown-guest {
    grid-column: 5 / 6;
  }

  // ----- row--3----- //
  // ----- column left ----- //
  &__expandable-checkbox-list {
    position: relative;
    top: -15px;
    z-index: 1;
    max-width: 266px;
  }

  &__expandable-checkbox-list-open {
    max-width: 266px;
  }

  // ----- row--3----- //
  // ----- column right ----- //
  &__bullet-list {
    max-width: 292px;
  }


  // ----- row--4----- //
  // ----- column left ----- //
  &__icon-text-list {
    margin-left: 3px;
    max-width: 277px;
  }

  // ----- row--4----- //
  // ----- column center ----- //
  &__comment {
    grid-column: 3 /6;
    margin-top: 7px;
  }
}

